import {ValuesType} from "./EditItemForm";

export const validate = (values: ValuesType, editForm: boolean) => {
    const errors: Partial<ValuesType> = {};

    if (!values.name && !editForm) {
        errors.name = ' ';
    } else if (values.name.length > 50) {
        errors.name = '  50   ';
    }

    if (!values.amount && !editForm) {
        errors.amount = ' ';
    }

    if(!values.price){
        errors.price = ' ';
    }

    return errors;
};